import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { SearchFacilityComponent } from './search-facility/search-facility.component';
import { ViolationService } from 'src/app/api/controllers/Violation';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { FormControl } from '@angular/forms';
import { SearcWorkpermitsComponent } from './searc-workpermits/searc-workpermits.component';
import { SearchParkingslotsComponent } from './search-parkingslots/search-parkingslots.component';
import { SearchBlockunitComponent } from './search-blockunit/search-blockunit.component';
import * as moment from 'moment';

@Component({
  selector: 'app-add-violation',
  templateUrl: './add-violation.component.html',
  styleUrls: ['./add-violation.component.scss']
})
export class AddViolationComponent implements OnInit {

  isViolationLoaded = false;
  errorMessage = '';
  isError = false;

  isAccountAdded = false;

  violationData={
    primaryContactName:'',
    UnitTower:'',
    ParkingSlot:'',
    violationCategory:'',
    violationRuleNo:'',
    violationComments:'',
    violationNoticedOn:'',
    violationOccurence:'',
    unitOwnerName:'',
    penaltyAmt:'',
    raisedBy:'',
    invoiceNo:'',
    invoiceTrigger:'',
    uploadImage:'',
    apartmentBlockUnitId:0
  }

  ruleListData: any;
  allViolationCategory: any;
  violationCategory: FormControl;
  violationRule: FormControl;

  selectedInput:string = "";
  columnField: any = {};
  fileList;

  constructor(
    private dialog: MatDialog,
    private violationService: ViolationService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    private cdr: ChangeDetectorRef
  ) { }

  ngOnInit(): void {
    this.isViolationLoaded = true;
    this.isAccountAdded = true;
    this.getViolationCategory();
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
      this.violationData.violationNoticedOn=moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  getDate(receipt, date){

    // this.violationData.map(obj=> {
    //    if(obj.violationNoticedOn == receipt.violationNoticedOn){
    //      obj.violationNoticedOn = moment(date).format("DD/MM/YYYY");
    //    }
    // });
 
     return moment(date).format("DD/MM/YYYY"); 
   }
 


  openSearchFacility(): void {
    const dialogRef = this.dialog.open(SearchFacilityComponent, {
      // width: '900px',
      data: {
        type: 'search-facility'
      }
    });
    dialogRef.componentInstance.outputEvent.subscribe((res) => {
      this.ongetSearchData(res);
      console.log(res.amount);
      this.violationData.penaltyAmt=res.amount;
    });
    dialogRef.afterClosed().subscribe(result => {
    });
  }


  
  openSearchWorkPermit(): void {
    const dialogRef = this.dialog.open(SearcWorkpermitsComponent, {
      // width: '900px',
      data: {
        type: 'search-workpermit'
      }
    });
    dialogRef.componentInstance.outputEvent.subscribe((res) => {
      this.ongetSearchData(res);
      this.violationData.apartmentBlockUnitId=res.apartmentBlockUnitId;
    });
    dialogRef.afterClosed().subscribe(result => {

    });
  }


   
  openSearchparkingSlot(): void {
    const dialogRef = this.dialog.open(SearchParkingslotsComponent, {
      // width: '900px',
      data: {
        type: 'search-facility'
      }
    });
    dialogRef.componentInstance.outputEvent.subscribe((res) => {
      this.ongetSearchData(res);
     
    });
    dialogRef.afterClosed().subscribe(result => {
     
    });
  }

  openSearchBlockUnit(): void {
    const dialogRef = this.dialog.open(SearchBlockunitComponent, {
      // width: '900px',
      data: {
        type: 'search-facility'
      }
    });
    dialogRef.componentInstance.outputEvent.subscribe((res) => {
      this.ongetSearchData(res);
    });
    dialogRef.afterClosed().subscribe(result => {

    });
  }


  ongetSearchData(data) {
    console.log(data);

  }


  getViolationRule(violationCateogoryId){
    
    const _apartment_Id = parseInt(this.cookieService.get('apartmentId'));

      const params1 = {
        apartmentId: _apartment_Id,
        ViolationCategoryId: violationCateogoryId
      }

      this.violationService.getViolationRule(params1).subscribe((rule:any) => {

        this.ruleListData = rule;
      },
      error => {

        this.isError = true;

      })

  }

  getViolationCategory(){
    let params = {
      LookupTypeId: 78,
      active: 1
    }
    this.lookupService.getLookupValueByLookupTypeIdStatus(params).subscribe((res:any) => {
      this.allViolationCategory = res;

    })
  }

  onChangeCategoryID(event){
    this.getViolationRule(event);
  }

  uploadImage_new(e) {
    this.fileList = e.dataTransfer ? e.dataTransfer.files : e.target.files;
    const reader = new FileReader();
    const filelist = [];

    if (this.fileList.length > 1) {
      // this.alert.toastWarning('Please select one image');
      return false;
    }

    if (this.fileList.length > 0) {

      for (const item of this.fileList) {
        filelist.push(item);
        // this.showImg = false;



        reader.onload = (e: any) => {
          const target = e.target;
          const fileResult = target.result;
          console.log(fileResult);
          this.violationData.uploadImage=fileResult;

      }
      reader.readAsDataURL(item);
      }
      this.cdr.markForCheck();
    }
  }

  onSubmit(){

    const params = {
      model: {
        /** format: int32 */
        id: 0,
        /** format: int32 */
        violationStatusId: 0,
        comments: this.violationData.violationComments,
        /** format: int32 */
        violationRuleId: parseInt(this.violationData.violationRuleNo),
        /** format: int32 */
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        /** format: int32 */
        apartmentBlockUnitId: 0,
        /** format: date-time */
        noticedOn: this.violationData.violationNoticedOn,
        /** format: double */
        penaltyAmt: parseInt(this.violationData.penaltyAmt),
        /** format: int64 */
        raisedby: parseInt(this.cookieService.get('userId')),
        /** format: int64 */
        invoiceNo: parseInt(this.violationData.invoiceNo == '' ? '0' : this.violationData.invoiceNo),
        isActive: true,
        /** format: int32 */
        insertedBy: parseInt(this.cookieService.get('userId')),
        /** format: date-time */
        insertedOn: new Date().toISOString(),
        /** format: int32 */
        updatedBy: parseInt(this.cookieService.get('userId')),
        /** format: date-time */
        updatedOn: new Date().toISOString(),
        /** format: int32 */
        violationOccurrence: parseInt(this.violationData.violationOccurence),
        primaryContact: this.violationData.primaryContactName,
        unitOwnerName: this.violationData.unitOwnerName,
        /** format: byte */
        uploadImage: this.violationData.uploadImage,
        /** format: int32 */
        apartmentBlockId: this.violationData.apartmentBlockUnitId,
        /** format: int32 */
        disputeId: 0,
        /** format: int32 */
        violationCategoryId: parseInt(this.violationData.violationCategory),
        violationCategory: this.violationData.violationCategory,
        tower_unitname: this.violationData.UnitTower,
        securityPerson: '',
        disputeStatus: 'true',
        disputeComments: ''
      }
    }


    this.violationService.addViolations(params).subscribe((res: any) => {

      if (res) {
        this.sharedService.setAlertMessage(res.message);
        this.violationData={
          primaryContactName:'',
          UnitTower:'',
          ParkingSlot:'',
          violationCategory:'',
          violationRuleNo:'',
          violationComments:'',
          violationNoticedOn:'',
          violationOccurence:'',
          unitOwnerName:'',
          penaltyAmt:'',
          raisedBy:'',
          invoiceNo:'',
          invoiceTrigger:'',
          uploadImage:'',
          apartmentBlockUnitId:0
        }
              }
      else {
      }

    });
  }
}
