  moveTabRight(){
  	if (this.currentPosition + 1 < this.itemPositions.length) {
      document.getElementById('details').style.left = '-' + this.itemPositions[++this.currentPosition] + 'px';
  	}

  }

  moveTabLeft(){
    if (this.currentPosition - 1 >= 0) {
      document.getElementById('details').style.left = '-' + this.itemPositions[--this.currentPosition] + 'px';
    }
  }


