  submitCreatesViolationPrivledgeForm(id) {
    const control = this.tableForm().at(id).get('violationTable') as FormArray;


    const lookupparams = {
      Id: this.privledge.privilegeId === 0 ? 0 : this.privledge.privilegeId,
      Name : this.privledge.ViolationName,
      Description : this.privledge.ViolationDescription,
      ViolationCategoryId: 0,
      FineViolationNo1: 0,
      DescViolationNo1: '',
      ViolationPrivilegeId1: 0,
      FineViolationNo2: 0,
      DescViolationNo2: '',
      ViolationPrivilegeId2: 0,
      FineViolationNo3: 0,
      DescViolationNo3: '',
      ViolationPrivilegeId3: 0,
      FineViolationNo4: 0,
      DescViolationNo4: '',
      ViolationPrivilegeId4: 0,
      FineViolationNo5: 0,
      DescViolationNo5: '',
      ViolationPrivilegeId5: 0,
      IsActive: true,
      InsertedBy: parseInt(this.cookieService.get('userId')),
      InsertedOn: new Date().toISOString(),
      UpdatedBy: parseInt(this.cookieService.get('userId')),
      UpdatedOn: new Date().toISOString(),
      ApartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    if (this.privledge.privilegeId === 0) {



      this.violationService.addViolationRule(lookupparams).subscribe((res: any) => {

        if (res) {
          this.sharedService.setAlertMessage(res.message);
          this.getViolationPrivilege();
          this.privledge = {
            privilegeId: 0,
            ViolationName: '',
            ViolationDescription: '',
            PeriodOfPrivledges: ''
          };
        }
        else {
        }

      });
    } else {

      this.violationService.updateViolationRule(lookupparams).subscribe((res: any) => {

        if (res) {
          this.sharedService.setAlertMessage(res.message);
          this.getViolationPrivilege();
          this.privledge = {
            privilegeId: 0,
            ViolationName: '',
            ViolationDescription: '',
            PeriodOfPrivledges: ''
          };
        }
        else {
        }

      });

    }
  }

