import { Component, OnInit, EventEmitter, Output, Inject, Input } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CookieService } from 'ngx-cookie-service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { ViolationService } from '../../../../../api/controllers/Violation';
import { LookupService } from '../../../../../api/controllers/Lookup';

@Component({
  selector: 'app-create-dispute',
  templateUrl: './create-dispute.component.html',
  styleUrls: ['./create-dispute.component.scss']
})
export class CreateDisputeComponent implements OnInit {

  config;
  dispute:any;
  @Input() violation: any;
  @Input() violationId: any;
  @Output() outputEvent = new EventEmitter<any>();
  constructor(
    public dialogRef: MatDialogRef<CreateDisputeComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private cookieService: CookieService,
    private sharedService: SharedService,
    private violationService: ViolationService,
    private lookupService: LookupService
  ) {
    this.config = data;
   }

  ngOnInit(): void {
    this.dispute = {
      disputeID: 0,
      disputeComments: '',
      disputeStatus:''
    };

  }

 

  submit() {
    const lookupparams = {
      model:{
        disputeId: this.dispute.disputeID,
        disputeComments: this.dispute.disputeComments,
        isActive: this.dispute.disputeStatus,
        insertedBy: parseInt(this.cookieService.get('userId')),
        insertedOn: new Date().toISOString(),
        updatedBy: parseInt(this.cookieService.get('userId')),
        updatedOn: new Date().toISOString(),
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        apartmentBlockUnitId: this.violation.apartmentBlockUnitId === null ? 1 : this.violation.apartmentBlockUnitId,
        apartmentBlockId: this.violation.apartmentBlockId ===null ? 1 :this.violation.apartmentBlockId
      }
    }


      this.violationService.addViolationDispute(lookupparams).subscribe((res: any) => {

        if (res) {
          this.sharedService.setAlertMessage(res.message);
          this.dispute = {
            disputeID: 0,
            disputeComments: '',
            disputeStatus:''
          };
        }
        else {
        }

      });

      this.updateviolationstatus(this.violation.id,402);

  }


  updateviolationstatus(id,statusid){
    let statusparams = {
      voilationId:id,
      statusId: statusid
    }

    this.violationService.updateViolationStatus(statusparams).subscribe((res:any) => {
      let list=res;
      this.sharedService.setAlertMessage(res.message);
      this.outputEvent.emit(true);


    });
  }

}
