import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViolationComponent } from './violation.component';
import { ViolationSetupComponent } from './components/violation-setup/violation-setup.component';
import { AddViolationComponent } from './components/add-violation/add-violation.component';
import { ViewViolationComponent } from './components/view-violation/view-violation.component';
import { StopPrivilegeComponent } from './components/stop-privilege/stop-privilege.component';


const routes: Routes = [
  { path: '', redirectTo: 'setup', pathMatch: 'full'},
  { path: 'setup', component: ViolationSetupComponent},
  { path: 'add-violation', component: AddViolationComponent},
  { path: 'view-violation', component: ViewViolationComponent},
  { path: 'stop-privilege', component: StopPrivilegeComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ViolationRoutingModule { }
