import { Component, OnInit, EventEmitter, Output, Inject, Input } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CookieService } from 'ngx-cookie-service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { ViolationService } from 'src/app/api/controllers/Violation';
import { ScreenService } from '../../../../../api/controllers/Screen';

@Component({
  selector: 'app-edit-privilege',
  templateUrl: './edit-privilege.component.html',
  styleUrls: ['./edit-privilege.component.scss']
})
export class EditPrivilegeComponent implements OnInit {

  config;
  privledge:any;
  @Input() privilegevalue: any;
  allMenus=[];

  @Output() outputEvent = new EventEmitter<any>();
  constructor(
    public dialogRef: MatDialogRef<EditPrivilegeComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private cookieService: CookieService,
    private sharedService: SharedService,
    private violationService: ViolationService,
    private screenService: ScreenService
  ) {
    this.config = data;
   }

  ngOnInit(): void {
    this.getallMenu();
    this.editViolationPrivilege(this.privilegevalue);
  }


  close(): void {
    this.dialogRef.close(false);
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getallMenu(){
    let params = {
      active: 1
    }
    this.screenService.getAllScreensByStatus(params).subscribe((res:any) => {
      const menuData = res;

      menuData.forEach((element: any) =>{
        let data=element.screenName;

       this.allMenus.push(data.replace("Admin_Main_", ""));

      });

    });
  }


  editViolationPrivilege(privilege) {

    let arr= privilege.description.split(',');
    this.privledge = {
      privilegeId: privilege.id,
      StopPrivledgeName: privilege.name,
      ViolationDescription: privilege.description.split(','),
      PeriodOfPrivledges: privilege.stopPriveldgeDays
    };

  }

  submitCreatesStopPrivledgeForm(form) {
    const lookupparams = {
      Id: this.privledge.privilegeId === 0 ? 0 : this.privledge.privilegeId,
      Name : this.privledge.StopPrivledgeName,
      Description : this.privledge.ViolationDescription.toString(),
      StopPriveldgeDays : this.privledge.PeriodOfPrivledges,
      IsActive: true,
      InsertedBy: parseInt(this.cookieService.get('userId')),
      InsertedOn: new Date().toISOString(),
      UpdatedBy: parseInt(this.cookieService.get('userId')),
      UpdatedOn: new Date().toISOString(),
      ApartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

      this.violationService.updateViolationPrivilege(lookupparams).subscribe((res: any) => {

        if (res) {
          this.sharedService.setAlertMessage("updated Successfully");
          this.dialogRef.close(false);
          this.outputEvent.emit(true);
          this.privledge = {
            privilegeId: 0,
            ViolationName: '',
            ViolationDescription: '',
            PeriodOfPrivledges: ''
          };
        }
        else {
        }

      });

 
  }

}
