import { Component, OnInit, Inject, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { WorkPermitService } from 'src/app/api/controllers/WorkPermit';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-searc-workpermits',
  templateUrl: './searc-workpermits.component.html',
  styleUrls: ['./searc-workpermits.component.scss']
})
export class SearcWorkpermitsComponent implements OnInit {

  facilityForm: FormGroup;

  allFacility: any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 20;
  totalFilterItems:any;

  unitFieldType = 'unitno';
  unitOrder = false;

  wpData: any;
  config;


  @Output() outputEvent = new EventEmitter<any>();




  constructor(
    public dialogRef: MatDialogRef<SearcWorkpermitsComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private facilityService: FacilityService,
    private cookieService: CookieService,
    private workpermitService: WorkPermitService
  ) {
    this.config = data;
   }

  ngOnInit(): void {
    this.getWorkPermit();
  }

  close(): void {
    this.dialogRef.close(false);
  }

  getWorkPermit(){
    const params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      active: 1
    }

    this.workpermitService.getWorkPermitsByApartmentIdStatus(params).subscribe((res:any) => {
      this.allFacility = res;

      this.totalItems = this.allFacility.length;
      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }
    })
  }


  getFacilityName(id){


    this.facilityService.getApartmentFacilityByFacilityId(id).subscribe((res:any) => {
      this.allFacility = res;


    });
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }



  onSelect(facility){
    console.log(facility);

    this.outputEvent.emit(facility);
    this.dialogRef.close();
  }


}
