import { Component, OnInit, EventEmitter, Output, Inject, Input } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CookieService } from 'ngx-cookie-service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { ViolationService } from '../../../../../api/controllers/Violation';
import { LookupService } from '../../../../../api/controllers/Lookup';

@Component({
  selector: 'app-edit-category',
  templateUrl: './edit-category.component.html',
  styleUrls: ['./edit-category.component.scss']
})
export class EditCategoryComponent implements OnInit {

  config;
  category:any;
  @Input() violationcategory: any;
  @Output() outputEvent = new EventEmitter<any>();
  constructor(
    public dialogRef: MatDialogRef<EditCategoryComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private cookieService: CookieService,
    private sharedService: SharedService,
    private violationService: ViolationService,
    private lookupService: LookupService
  ) {
    this.config = data;
   }

  ngOnInit(): void {
    this.category = {
      categoryId: 0,
      categoryname: '',
      Description: ''
    };
    this.editViolationCategory(this.violationcategory);
  }

  editViolationCategory(violationcategory) {

    this.category = {
      categoryId: violationcategory.lookupValueId,
      categoryname: violationcategory.lookupValueName,
      Description: violationcategory.description
    }

  }

  submitCreateCategoryTypeForm(form) {
    const lookupparams = {
      lookupvalue:
      {
      lookupValueId: this.category.categoryId === 0 ? 0 : this.category.categoryId,
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      lookupTypeId: 78,
      lookupValueName: this.category.categoryname,
      description: this.category.Description,
      isActive: true,
      insertedBy: parseInt(this.cookieService.get('userId')),
      insertedOn: new Date().toISOString(),
      updatedBy: parseInt(this.cookieService.get('userId')),
      updatedOn: new Date().toISOString(),
      notes: '',
      isCommon: true,
      module: '',
      subModule: ''

    }
    }


      this.lookupService.updateLookupValue(lookupparams).subscribe((res: any) => {

        if (res) {
          this.sharedService.setAlertMessage("Violation Category Updated Successfully");
          this.category = {
            categoryId: 0,
            categoryname: '',
            Description: ''
          };
        }
        else {
        }

      });


  }

}
